	vertical-align: baseline;
	font-weight: inherit;
	font-family: inherit;
	font-style: inherit;
	font-size: 100%;
	border: 0 none;
	outline: 0;
	padding: 0;
	margin: 0;
}

a {
    color: black;
    text-decoration: none;
}

body {
    height: 100vh;
    width: 100vw;
}

.header-container {
    background-color: rgba(0, 0, 255, .9);
    height: 15%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

#header-text {
    font-size: 3rem;
}

.start-menu-container {
    position: absolute;
    z-index: 4;
    height: 50%;
    width: 90%;
    top: 12%;
    left: 5%;
    background-color: darkblue;
    border: 1px solid black;
    box-shadow: .5rem .5rem 1rem black;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.start-menu-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    gap: 10%;
}

.player1-menu-container, .player2-menu-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: .5rem;
}

.label {
    font-weight: bold;
    font-size: 1.3rem;
    color: paleturquoise;
}

.start-menu-form p {
    color: paleturquoise;
}

.start-menu-button-container {
    display: flex;
    justify-content: center;
    gap: 15%;
}

.start-menu-button-container div {
    display: flex;
    flex-direction: column;
    width: 25%;
    align-items: center;
    text-align: center;
    gap: .5rem;
}

.start-menu-form input {
    border-radius: .5rem;
    padding: .2rem;
}



.gameover-container {
    position: absolute;
    z-index: 3;
    height: 50%;
    width: 90%;
    left: 5%;
    top: 20%;
    background-color: darkblue;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 10%;
    border: 1px black solid;
}

.gameover-text {
    font-weight: bold;
    font-size: 2.3rem;
    color: paleturquoise;
}

button {
  align-items: center;
  appearance: none;
  background-image: radial-gradient(100% 100% at 100% 0, #5adaff 0, #5468ff 100%);
  border: 0;
  border-radius: 6px;
  box-shadow: rgba(45, 35, 66, .4) 0 2px 4px,rgba(45, 35, 66, .3) 0 7px 13px -3px,rgba(58, 65, 111, .5) 0 -3px 0 inset;
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  display: inline-flex;
  height: 48px;
  justify-content: center;
  line-height: 1;
  list-style: none;
  overflow: hidden;
  padding-left: 16px;
  padding-right: 16px;
  position: relative;
  text-align: left;
  text-decoration: none;
  transition: box-shadow .15s,transform .15s;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  white-space: nowrap;
  will-change: box-shadow,transform;
  font-size: 18px;
}

button:focus {
  box-shadow: #3c4fe0 0 0 0 1.5px inset, rgba(45, 35, 66, .4) 0 2px 4px, rgba(45, 35, 66, .3) 0 7px 13px -3px, #3c4fe0 0 -3px 0 inset;
}

button:hover {
  box-shadow: rgba(45, 35, 66, .4) 0 4px 8px, rgba(45, 35, 66, .3) 0 7px 13px -3px, #3c4fe0 0 -3px 0 inset;
  transform: translateY(-2px);
}

button:active {
  box-shadow: #3c4fe0 0 3px 7px inset;
  transform: translateY(2px);
}

.main-container {
    height: 77%;
    width: 100%;
    background-color: rgba(0, 0, 0, .8);
    display: grid;
    grid-template-columns: 5% 95%;
    grid-template-rows: 10% 5% 5% 60% 1.7% 10%;
}

.turn-announcement-container {
    grid-area: 1 / 1 / 2 / 3;
    text-align: center;
    font-weight: bold;
}

.turn-announcement-container h2 {
    font-size: 2rem;
}

.board-info-container {
    display: flex;
    grid-area: 2 / 1 / 3 / 3;
    align-items: end;
    justify-content: center;
    font-weight: bold;
}

.grid-container {
    align-self: center;
    display: flex;
    flex-direction: column;
    height: 80%;
    width: 100%;
    align-items: center;
    justify-content: center;
    grid-area: 4 / 2 / 5 / 3;
}

.column-labels-container {
    font-weight: bold;
    display: grid;
    grid-template-columns: repeat(10, 2rem);
    grid-area: 3 / 2 / 4 / 3;
    align-items: end;
    justify-content: center;
    text-align: center;
    gap: .1rem;
}

.row-labels-container {
    align-self: center;
    font-weight: bold;
    display: grid;
    grid-area: 4 / 1 / 5 / 2;
    align-items: center;
    justify-content: end;
    text-align: center;
    grid-template-rows: repeat(10, 2rem);
    gap: .15rem;
}

.grid {
    display: grid;
    height: min-content;
    width: min-content;
    grid-template-columns: repeat(10, 1fr);
    grid-template-rows: auto;
    gap: 0;
    align-items: center;
    justify-content: center;
}

.cell {
    height: 2rem;
    width: 2rem;
    border: black solid 1px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.Battleship {
    position: relative;
    left: 3.3rem;
    width: 8rem;
}

.Carrier {
    position: relative;
    left: 4.3rem;
    width: 10rem;
}

.Cruiser {
    position: relative;
    left: 2.2rem;
    width: 6rem;
}

.Destroyer {
    width: 4rem;
    position: relative;
    left: 1rem;
}

.Submarine {
    z-index: 1;
    position: relative;
    left: 2rem;
    width: 6rem;
}

.vertical .Battleship {
    left: 0;
    top: 3.3rem;
}

.vertical .Carrier {
    left: 0;
    top: 4.4rem;
}

.vertical .Cruiser {
    left: 0;
    top: 2.2rem;
}

.vertical .Destroyer {
    left: 0;
    top: 1.1rem;
}

.vertical .Submarine {
    left: 0;
    top: 2.2rem;
}

.cell:hover {
    border: darkblue solid 1px;
}

.error-message {
    display: flex;
    grid-area: 5 / 1 / 6 / 3;
    color: red;
    justify-content: center;
    font-size: .85rem;
}

.sunk-ships-container {
    grid-area: 6 / 1 / 7 / 3;
    display: flex;
    flex-direction: column;
    width: 40%;
}

.sunk-ships-container li {
    font-size: .8rem;
}

.sunk-ships-container h4 {
    font-size: 1.1rem;
}

.player-sunk-ships-container ul {
    list-style-type: none;
    height: 70%;
    display: flex;
    flex-direction: column;
    /* flex-wrap: wrap; */
}

.footer-container {
    background-color: rgba(0, 0, 255, .9);
    height: 8%;
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    padding-right: .5%;
}

.credit-container {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: flex-end;
    padding-bottom: .3%;
}

span.is-hit {
    height: 1rem;
    width: 1rem;
    background-color: red;
    border-radius: 50%;
    display: inline-block;
}

.cell.has-ship.is-hit span {
    display: none;
}

.explosion {
    position: absolute;
    height: 2rem;
    width: 2rem;
    z-index: 1;
}

.vertical img {
    transform: rotate(270deg);
}

.hidden {
    display: none;
